@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@500&display=swap);
/* 'Yanone Kaffeesatz' */
/* 'Noto Sans KR' */
/* 'Noto Serif KR' */

@font-face {
  font-family: "UhBeeDoolDool";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_five@.2.0/UhBeeDoolDool.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --bg: #fdfdff;
  --bg-accent: #9db5d8;
  --bg-smooth: #f6f4f3;
  --text-color: #090c08;
  --font: "Noto Sans KR", Georgia, "Malgun Gothic", sans-serif;
  --serif-font: "Noto Serif KR", serif;
  --main-color: #db2b39;
  --point-color: #276fbf;
  --nav-size: 80px;
  --width: 1120px;
  --border: 1px solid #474a4d;
  --border-radius: 1em;
  --speed: 500ms;
  --letter-spacing: 0.1em;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  height: 100%;
  background-color: #fdfdff;
  background-color: var(--bg);
  font-family: "Noto Sans KR", Georgia, "Malgun Gothic", sans-serif;
  font-family: var(--font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.body-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  flex: 1 0 auto;
}

footer {
  flex-shrink: 0;
  width: 100vw;
  /* height: 5em; */
  bottom: 0;
  color: #222222;
  background-color: #ebebeb;
  font-size: 0.8em;
  padding: 0.7em 1em;
  text-align: right;
}
footer p,
address {
  margin: 0 0 0.3em 0;
}
ul {
  list-style: none;
  padding: 0;
}

a {
  margin: 0;
  padding: 0;
}

button {
  border-radius: 1em;
  border-radius: var(--border-radius);
  display: inline-block;
  letter-spacing: calc(0.1em * 1.2);
  letter-spacing: calc(var(--letter-spacing) * 1.2);
}

li a {
  text-decoration: none;
  display: block;
  letter-spacing: 0.1em;
  letter-spacing: var(--letter-spacing);
}

.wrapper {
  padding: 1em;
}

.page-title {
  margin: 1em auto 1em;
  font-weight: bold;
  letter-spacing: 0.1em;
  letter-spacing: var(--letter-spacing);
  text-align: center;
  font-size: 2em;
}

/* ---------------HEADER--------------- */

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  margin: 0;
  flex: none;
}

header .logo {
  color: #db2b39;
  color: var(--main-color);
  font-family: "Yanone Kaffeesatz";
  font-weight: bold;
  font-size: 3.3em;
  letter-spacing: calc(0.1em * 0.7);
  letter-spacing: calc(var(--letter-spacing) * 0.7);
  margin: 0 0.4em;
}

nav {
  position: absolute;
  background-color: #f6f4f3;
  background-color: var(--bg-smooth);
  top: 0;
  right: 0;
  width: 45%;
  height: 100vh;
  z-index: 1;
  visibility: hidden;
}
.open-nav {
  visibility: unset;
}

.open {
  cursor: pointer;
  width: 2.5em;
  margin-right: 0.7em;
}

.exit {
  color: #db2b39;
  color: var(--main-color);
  width: 3em;
  float: right;
  cursor: pointer;
  margin: 1em 0.5em;
}

nav svg {
  width: 36px;
  height: 36px;
}
nav ul {
  margin-top: 4em;
}

nav ul li a,
.submenu li > * {
  color: #db2b39;
  color: var(--main-color);
  text-decoration: none;
  display: block;
  padding: 0.5em 1em;
  font-size: 1.3em;
  letter-spacing: 0.1em;
  letter-spacing: var(--letter-spacing);
}

li a:hover,
li span:hover {
  background: #db2b39;
  background: var(--main-color);
  font-weight: bolder;
  color: #f6f4f3;
  color: var(--bg-smooth);
  cursor: pointer;
}

/* ---------------------LANDING------------------ */

.bgr-img {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: -1;
  background: url(/static/media/bg-img-2.544badbb.jpg) no-repeat center center fixed;
  background-size: cover;
  height: 100%;
  overflow: hidden;
}

.intro {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.intro h1 {
  font-size: 2.6em;
  font-weight: bold;
  width: 80%;
  margin: 1em auto 0.5em;
  line-height: 1.5em;
  color: #f6f4f3;
  color: var(--bg-smooth);
}

.intro h1 span {
  font-size: 1.2em;
  font-weight: bolder;
}

.intro p {
  font-size: 1em;
  padding: 0 0.5em;
  line-height: 1.8em;
  margin-bottom: 0;
  background-color: #f6f4f3;
  background-color: var(--bg-smooth);
}

.intro button {
  background-color: #276fbf;
  background-color: var(--point-color);
  border: none;
  padding: 0.6em 1.2em;
  margin: 1.5em 0.5em 2em;
  font-size: 1.05em;
}
.intro button a {
  color: #f6f4f3;
  color: var(--bg-smooth);
}

h1 strong {
  color: #db2b39;
  color: var(--main-color);
}

/* Dropdown Menu */

.submenu {
  margin: 0;
  font-size: 0.8em;
}
.submenu ul {
  margin-top: 0.5em;
}
.submenu li {
  padding: 0;
}
.submenu li > * {
  padding: 0.4em 2em;
}

/* ----------------CANVAS-OPTION--------------- */
.posting {
  margin: auto 1.2em;
  width: 100%;
  padding: 1em;
}

.posting > * {
  margin-bottom: 1.5em;
}

.canvas-background {
  display: block;
}
.canvas-background > * {
  margin-bottom: 2em;
}

.bgr-image {
  width: 8em;
  height: 8em;
  margin: 0 auto 2em;
}
.ant-upload.ant-upload-select-picture-card {
  width: 100%;
  height: 100%;
  /* overflow: hidden; */
  margin: 0;
}
.ant-upload.ant-upload-select-picture-card > .ant-upload {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.preview-image {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.bgr-color label {
  font-size: 1.2em;
}

.canvas-font {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: wrap;
}

.font {
  font-size: 2.4em;
}
.font input[type="range"] {
  margin-left: 0.2em;
}

.sketch-picker {
  position: absolute;
  z-index: 1;
  right: 0;
  /* right: 0.7em; */
}

.content textarea {
  resize: none;
  width: 100%;
}

.upload-btn {
  text-align: right;
}
.upload-btn button {
  margin: 0;
  color: #f6f4f3;
  color: var(--bg-smooth);
  background-color: #276fbf;
  background-color: var(--point-color);
  border: none;
  padding: 0.6em 1.2em;

  font-size: 1.05em;
}

/* ----------------CANVAS--------------- */
.canvas-wrapper {
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.canvas {
  position: relative;
  width: 100%;
  max-width: 400px;
  height: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
  overflow: hidden;
}
.canvas-bgr {
  width: 100%;
  height: 100%;
  position: relative;
}

.canvas-bgr img {
  position: absolute;
  height: 400px;
  width: 100%;
  cursor: move;
  object-fit: cover;
}

.quote-textarea {
  font-family: "Noto Sans KR", Georgia, "Malgun Gothic", sans-serif, "Google Sans", Roboto, Arial, "Apple SD Gothic Neo",
    sans-serif;
  font-family: var(--font), "Google Sans", Roboto, Arial, "Apple SD Gothic Neo",
    sans-serif;
  font-weight: normal;
  line-height: 1.2em;
  background-color: transparent;
  position: absolute;
  top: 50%;
  left: 50vh;
  border: none;
  resize: both;
  width: 13em;
  overflow: auto;
  transform: translate(-50%, -50%);
  cursor: -webkit-grab;
  cursor: grab;
}

/* ------------REGISTER, SUBMIT------------ */

.form {
  width: 60%;
}

.register form > *,
.login form > * {
  display: block;
  width: 100%;
}

.form-control label::after {
  content: " ＊";
  color: #db2b39;
  font-size: 0.1em;
}

.form-control {
  position: relative;
  margin-top: 0.8em;
}
.form-control input {
  width: 100%;
  border: 2px solid #9db5d8;
  border: 2px solid var(--bg-accent);
  border-radius: calc(1em * 0.3);
  border-radius: calc(var(--border-radius) * 0.3);
  padding: 0.3em;
  outline: none;
}
.form-control input:focus {
  outline: none;
  border-color: #474a4d;
}
.form-control input::-webkit-input-placeholder {
  font-size: 0.8em;
}
.form-control input:-ms-input-placeholder {
  font-size: 0.8em;
}
.form-control input::-ms-input-placeholder {
  font-size: 0.8em;
}
.form-control input::placeholder {
  font-size: 0.8em;
}

.form-control.success input {
  border-color: green;
}
.form-control.error input {
  border-color: #db2b39;
}

.form-control .icon {
  position: absolute;
  visibility: hidden;
  right: 0.8em;
  top: 2.2em;
}
.form-control .icon-suc {
  color: green;
}
.form-control .icon-err {
  color: #db2b39;
}
.form-control.success .icon-suc {
  visibility: visible;
}
.form-control.error .icon-err {
  visibility: visible;
}

.submit-btn {
  background-color: #347fd5;
  height: unset;
  border: none;
  padding: 0.6em 0.8em;
  border-radius: 1em;
  border-radius: var(--border-radius);
  margin: 1.5em 0 1em;
  display: inline-block;
  letter-spacing: calc(0.1em * 1.2);
  letter-spacing: calc(var(--letter-spacing) * 1.2);
  font-size: 1.05em;
  color: #f6f4f3;
  color: var(--bg-smooth);
}

/* ----------------A POST--------------- */
.a-post {
  border: 1px solid black;
  padding: 0.5em;
  margin: 0.1em;
}
.a-post-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.a-post .delete {
  font-size: 1.2em;
}
.a-post > * {
  margin-bottom: 0.5em;
}
.a-post > :last-child {
  margin-bottom: 0;
}
.a-post .content p {
  margin: 0;
  font-size: 1.2em;
}
.a-post .user a {
  font-size: 1.5em;
  color: #090c08;
  color: var(--text-color);
  font-weight: bold;
}
.a-post .user:hover a {
  color: #276fbf;
  color: var(--point-color);
}
.a-post .image {
  display: flex;
  justify-content: center;
}

.a-post .likes {
  font-size: 1.3em;
}

/* ----------------POST-THUMBNAIL--------------- */
.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 4em auto;
}
.profile .posts-list {
  margin: 0.8em;
  padding: 0.8em;
}

/* ----------------MODAL--------------- */

#modal {
  position: relative;
  z-index: 999;
}
.modal {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.error-message {
  width: 20em;
  padding: 1em;
  text-align: center;
  background-color: #f6f4f3;
  background-color: var(--bg-smooth);
  border-radius: 1em;
  border-radius: var(--border-radius);
}
.error-message .submit-btn {
  margin: 0.5em 0;
  padding: 0.5em 1.2em;
  background-color: #db2b39;
}

@media (min-width: 800px) {
  li a:hover,
  li span:hover {
    background: transparent;
    font-weight: bolder;
    color: #5695dc;
    cursor: pointer;
  }

  header .logo {
    font-size: 4.3em;
  }

  .landing {
    display: flex;
    height: 100%;
  }
  .intro {
    font-size: 1em;
    width: 50vw;
    left: 0;
    transform: translate(0, -50%);
  }
  .intro h1 {
    font-size: 3em;
    color: #090c08;
    color: var(--text-color);
  }

  .intro p {
    font-size: 1.3em;
    padding: 0 0.5em;
    color: #090c08;
    color: var(--text-color);
    background-color: unset;
  }

  .intro button {
    font-size: 1.2em;
    margin: 1.3em 0.5em 0;
  }

  .bgr-img {
    background-attachment: unset;
    height: 100vh;
    left: unset;
    right: 0;
    width: 50vw;
  }

  .open,
  .exit {
    display: none;
  }
  nav {
    visibility: unset;
    background: none;
    position: unset;
    height: auto;
    width: auto;
  }
  nav ul {
    display: flex;
    margin-top: auto;
    width: auto;
    float: right;
    margin: 0;
  }
  nav ul li a {
    display: block;
    padding: 1em;
    font-size: 1.9em;
    font-weight: bold;
  }

  header {
    overflow: visible;
  }

  .sub {
    position: relative;
  }
  .submenu {
    display: none;
    font-size: unset;
    font-weight: bold;
  }
  .sub:hover .submenu {
    display: flex;
    position: absolute;
    top: 6em;
    right: 0;
    background: white;
    /* padding: 0.5em; */
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.07);
  }
  .submenu ul {
    display: block;
    margin: 0;
  }
  .submenu ul li:last-child {
    position: relative;
  }
  .submenu ul li:last-child::before {
    position: absolute;
    content: "";
    width: auto;
    height: 1px;
    background-color: #db2b39;
    background-color: var(--main-color);
    left: 1em;
    right: 1em;
  }

  .submenu li > * {
    padding: 0.5em 2em;
  }

  .form {
    max-width: 25em;
  }

  .ant-card {
    max-width: 50em;
  }

  .posting {
    display: grid;
    grid-template-columns: 32em 32em;

    grid-template-rows: 5em 30em 6em 3em;
    grid-row-gap: 3em;
    justify-content: center;
    align-items: center;
  }
  .posting h1 {
    grid-area: 1 / 1 / 2 / 3;
  }
  .canvas-background {
    grid-area: 2 / 2 / 3 / 3;
  }
  .canvas-wrapper {
    grid-area: 2 / 1 / 3 / 2;
  }
  .content {
    grid-area: 3 / 2 / 4 / 3;
  }
  .upload-btn {
    grid-area: 4 / 2 / 5 / 3;
  }
  .sketch-picker {
    right: unset;
  }

  .infinite-scroll-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .body-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .a-post {
    padding: 1em;
  }
}

@media (min-width: 1200px) {
  .intro h1 {
    margin-top: 0.5em;
  }
  .intro h1 br {
    display: none;
  }
  .intro span {
    margin-left: 0.3em;
  }
}

#paper {
  width: auto;
  position: relative;

  padding: 3em 0;
  background-color: white;
  box-shadow: 0px 0px 1px 0px #888;
}

/* styling red vertical line */
#paper::after {
  content: "";
  width: 2px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 2em;
  background-color: rgba(255, 0, 0, 0.6);
}

/* title + (img + description) */
.card-content {
  display: flex;
  flex-direction: column;
}

.card-title {
  width: 100%;
  margin: 0 0 0 0.7em;
  font-weight: bold;
  letter-spacing: 0.3em;
}
/* styling blue horizontal lines */
#pattern {
  /* height: 100%; */
  display: flex;
  flex-flow: row wrap;
  position: relative;
  padding: 0 0 0 2em;
  background-origin: content-box;
  background-image: repeating-linear-gradient(teal 0px, white 2px, white 32px);
}

#content > * {
  margin: 0;
  padding: 0;
  font-size: 1em;
}

/* styling text content */
#content {
  position: relative;
  padding: 0 15px;
  line-height: 32px;
  font-size: 18px;
  letter-spacing: 1px;
  word-spacing: 3px;
}

#content h1 {
  font-size: 20px;
  color: red;
  margin: 0;
}

@media (min-width: 800px) {
  #paper {
    width: 40em;
  }
}

.postcard {
  margin: 0.8em;
  padding: 0.8em;
  border: solid #090c08 1px;
}
.post {
  width: 100%;
}

.postcard .user a {
  font-weight: bold;
  color: #090c08;
}
.postcard img {
  width: 100%;
}

.postcard .features {
  display: flex;
  align-items: center;
  margin-top: 0;
}

.features li a {
  padding: 0;
  font-size: unset;
  letter-spacing: unset;
}
.features li {
  margin-right: 0.5em;
}
.features li > * {
  display: flex;
  align-items: center;
}
.features span {
  font-size: 1.1em;
  margin-right: 0.2em;
  color: #db2b39;
}

.postcard > * {
  margin-bottom: 0.5em;
}
.postcard > :last-child {
  margin-bottom: 0;
}

.comments ul {
  margin: 0.5em 0;
}
.comment-form {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.comment-form input {
  width: 85%;
  margin-right: 3%;
}
.comment-form button {
  width: 12%;
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: #347fd5;
  border: solid 1px #347fd5;
  font-size: 0.8em;
}

@media (min-width: 800px) {
  .postcard {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 1em;
    -webkit-column-gap: 1em;
            column-gap: 1em;
    max-width: 840px;
    justify-content: center;
    align-content: center;
  }
  .postcard > * {
    margin: 0;
  }
  .postcard .user {
    grid-area: 1 / 2 / 2 / 3;
    font-size: 1.2em;
  }
  .postcard .post {
    grid-area: 2 / 1 / 5 / 2;
    min-width: 400px;
  }
  .postcard .content {
    grid-area: 2 / 2 / 4 / 3;
  }
  .postcard .features {
    grid-area: 5 / 1 / 6 / 2;
    font-size: 1.2em;
  }

  .postcard .comment-form {
    grid-area: 5 / 2 / 6 / 3;
  }

  .postcard .comments-list {
    grid-area: 3 / 2 / 5 / 4;
  }
}

